<template>
	<view>
		<view class="top">
			<view style="background-color: #7900B2;width: 100%;height: 128px;">
				<view style="width: 100%;" :style="'height:' + system_top + 'px;'"></view>
				<view class="row" style="width: 100%;height: 40px;align-items: center;">
					<image @click="doBack()" src="../../../../static/back-white.png" mode="aspectFit"
						style="width: 32px;height: 32px;margin-left: 12px;"></image>
					<view style="margin-left: 5px;flex: 1;" class="column">
						<view style="color: #FFF;font-size: 16px;font-weight: bold;height: 20px;line-height: 20px;">
							{{i18n.toher.daojiafuwu}}
						</view>
					</view>
					<image src="../../../../static/shoucang.png" mode="" style="width: 32px;height: 32px;"
						:style="'margin-right:' + weixinMenuWidth + 'px;'"></image>
				</view>
			</view>
			<view class="row topSearchBtn" @click="doSearch()">
				<image src="../../../../static/search.png" mode="aspectFit"
					style="margin-left: 10px;width: 20px;height: 20px;"></image>
				<view style="margin-left: 10px;font-size: 14px;color: #6F6F6F;">
					{{i18n.index.search}}
				</view>
			</view>
		</view>
		<view class="bigImage">
			<swiper class="index-banner swiper" circular="false" :indicator-dots="indicatorDots" :autoplay="autoplay"
				:interval="interval" :duration="duration">
				<swiper-item class="index-banner-img" @click="bannerTo(item)" v-for="(item, index) in bannerList"
					:key="item.id">
					<image class="index-banner-img" :src="item.image" mode="aspectFill"></image>
				</swiper-item>
			</swiper>
		</view>
		<view class="row" style="width: 100%;margin-top: 20px;flex-wrap: wrap;">
			<view v-for="(item,index) in itemArray" :key="index" class="classItem" @click="doClass(item)">
				{{item}}
			</view>
		</view>
		<view class="smallImg_back row">
			<image class="smallImage" src="" mode=""></image>
			<image class="smallImage" style="margin-left: 10px;" src="" mode=""></image>
		</view>
		<view style="width: 60%;height: 60px;align-items: center;line-height: 60px;padding-left: 15px;">
			{{i18n.toher.fujindianpu}}
		</view>
		<storeList :height="scrollHeight" typeString="4" :listArray="storeArray" @gotoCell="gotoCell"></storeList>

	</view>
</template>

<script>
	import storeList from '../../../../components/storeList/index.vue'
	
	export default {
		components: {
			storeList
		},
		data() {
			return {
				scrollHeight: 0,
				system_top: 44,
				weixinMenuWidth: 12,
				scrollHeight: 0,
				itemArray: ['萨瑟', '到大三大四', '请问请问', '得到的', '王尔德v', 'wewd'],
				bannerList: [{
					image: '../../../../static/logo.png'
				}, {
					image: '../../../../static/logo.png'
				}, {
					image: '../../../../static/logo.png'
				}, {
					image: '../../../../static/logo.png'
				}],
				storeArray:[]
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			const res = uni.getSystemInfoSync();

			// #ifdef MP-WEIXIN
			let weinfo = uni.getMenuButtonBoundingClientRect();
			this.weixinMenuWidth = weinfo.width + 12
			// #endif
			if (parseInt(res.safeArea.top) > this.system_top) {
				this.system_top = parseInt(res.safeArea.top)
			}
			this.scrollHeight = res.windowHeight - 60 - this.system_top
		},
		onLoad(option) {
			this.returnIndex = option.rdex
			this.requestData()
		},
		methods: {
			requestData() {

			},
			doBack() {
				uni.navigateBack({

				})
			},
			doSearch() {
				uni.navigateTo({
					url: '../../../../package-meishi/pages/search/index'
				})
			},
			bannerTo(item) {

			},

			doClass(item) {
				uni.navigateTo({
					url:'../../../../package-meishi/pages/class/class?name=' + item
				})
			},
			gotoCell(item) {
				uni.navigateTo({
					url:'../detaile/index'
				})
			}
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #f5f6f7;
	}

	.top {
		width: 100%;
		height: 160px;
		position: relative;

	}

	.topSearchBtn {
		position: absolute;
		left: 15px;
		right: 15px;
		bottom: 15px;
		height: 40px;
		border-radius: 8px;
		background-color: #FFF;
		/* box-shadow: rgba(50,50,59,0.1) 10px 10px; */
		-moz-box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		-webkit-box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		align-items: center;
	}

	.bigImage {
		margin-left: 15px;
		width: calc(100% - 30px);
		border-radius: 8px;
		height: 130px;
		background-color: #7900B2;
		transform: translateY(0);
		overflow: hidden;
	}

	.index-banner-img {
		width: 100%;
		height: 100%;
		border-radius: 8px;

	}

	.index-banner image {
		width: 100%;
		height: 100%;
		border-radius: 8px;
	}

	.classItem {
		margin-left: 16px;
		margin-bottom: 10px;
		height: 24px;
		border-radius: 12px;
		background-color: #FFF;
		text-align: center;
		font-size: 12px;
		font-weight: bold;
		color: #7900B2;
		line-height: 24px;
		padding-left: 10px;
		padding-right: 10px;
	}

	.smallImg_back {
		margin-top: 10px;
		margin-left: 15px;
		width: calc(100% - 30px);
		height: 80px;
	}

	.smallImage {
		width: calc(50% - 5px);
		border-radius: 8px;
		height: 80px;
		background-color: #7900B2;
	}
</style>
